body {
  margin: 0;
  padding: 0;
  background-color: #F0F2F4;
  font-size: 14px;
  font-family: 'Lato', 'sans-serif';
}
[v-cloak] {
  display: none;
}
.wepos-clearfix:before,
.wepos-clearfix:after {
  display: table;
  content: " ";
}
.wepos-clearfix:after {
  clear: both;
}
.wepos-right {
  float: right !important;
}
.wepos-left {
  float: left !important;
}
.wepos-hide {
  display: none;
}
.spinner {
  position: absolute;
  width: 125px;
  height: 125px;
  top: 40%;
  left: 45%;
}
.spinner:before,
.spinner:after {
  content: "";
  display: block;
  position: absolute;
  border-width: 5px;
  border-style: solid;
  border-radius: 50%;
  opacity: 1;
}
@keyframes scale-2 {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  50% {
    transform: scale(0.7);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
.spinner.spinner-loading:before {
  width: 117px;
  height: 117px;
  border-color: #3b80f4;
  top: 0px;
  left: 0px;
  animation: scale-2 1s linear 0s infinite;
}
.spinner.spinner-loading:after {
  width: 117px;
  height: 117px;
  border-color: #3b80f4;
  top: 0;
  left: 0;
  opacity: 0;
  animation: scale-2 1s linear 0.5s infinite;
}
.lds-ripple {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}
.product-variation {
  padding: 0;
  border-radius: 5px;
  box-shadow: 0px 4px 25px 1px rgba(0, 0, 0, 0.06);
  border-radius: 3px;
  margin-top: 45px;
  z-index: 99999;
  margin-left: 5px;
}
.product-variation .variation-header {
  padding: 10px 15px;
  border-bottom: 1px solid #E0E3E6;
  text-align: left;
  font-size: 18px;
}
.product-variation .variation-body {
  padding: 10px 15px 0px;
}
.product-variation .variation-body .attribute {
